import { getInfo } from '../../utils/UNSAFE_visUtils';
import { useChartDrill } from '../PRIVATE_useChartDrill';
import { useSelection } from '../PRIVATE_useVisSelection/useSelection';
import { mergeProps } from '../../utils/UNSAFE_mergeProps';
import { ChartItemData } from '../../utils/UNSAFE_visTypes/chart';

type DrillProps<K, D extends ChartItemData<K>> = Parameters<typeof useChartDrill<K, D>>[0];

type SelectionProps<K, D> = {
  selection: K[];
  onChange?: (detail: { ids: K[]; data: (D | undefined)[] }) => void;
  getDataById: (id: K) => D | undefined;
};

export function useSelectDrill<K, D extends ChartItemData<K>>({
  selectionMode,
  selection,
  onChange,
  rootRef,
  drilling,
  onItemDrill,
  onGroupDrill,
  focusedGroupInfo,
  focusedItemInfo,
  groups,
  getDataItem,
  getDataById
}: DrillProps<K, D> & SelectionProps<K, D>) {
  const idExtracter = (event: Event): K | undefined => {
    const isItemFocused = focusedItemInfo.isCurrent;
    const info =
      isItemFocused && event.type === 'keyup'
        ? focusedItemInfo
        : getInfo(rootRef, event.target as HTMLElement);
    return info && info.seriesIndex != null && info.groupIndex != null
      ? getDataItem(info.seriesIndex, info.groupIndex)?.id
      : undefined;
  };

  const { selectionProps, selectionContent } = useSelection<K, D>({
    idExtracter,
    isDrillEnabled: drilling === 'on',
    selection,
    selectionMode,
    onChange,
    getDataById
  });

  const drillProps = useChartDrill<K, D>({
    rootRef,
    selectionMode,
    drilling,
    focusedItemInfo,
    focusedGroupInfo,
    groups,
    getDataItem,
    onItemDrill,
    onGroupDrill
  });

  return { selectDrillProps: mergeProps(selectionProps, drillProps), selectionContent };
}
